@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans&display=swap);

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app{
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  overflow-x: hidden;
}

.header{
    background: #563d7c;
    height: 64px;
    width: 100%;
    display: flex;
    flex-direction: row;
    position: fixed;
    padding-top: 10px;
    padding-left: 8px;
}


.aditional{
    padding-right: 10px;
    color: #c4b4db;
    padding-bottom: 5px;
}

.aditional span{
    font-size: 16px;
    font-family: 'Noto Sans', sans-serif;
    text-decoration: none;
    padding: 5.5px;
    cursor: pointer;
}

.aditional span i{
    font-size: 18px;
    font-weight: 600;
    padding-left: 2px;
}
.aditional span:hover{
    color: #e9ecef;
}

.aditional i{
    font-size: 18px;
    padding-left: 15px;
    cursor: pointer;
}

.aditional i:hover{
    color: #e9ecef;
}

.aditional button{
    font-size: 15px;
    border: 1px solid #ffe484;
    background: #563d7c;
    border-radius: 5px;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 8px;
    padding-bottom: 10px;
    color:#ffe484 ;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 590;
    margin-left: 20px;
    cursor: pointer;
}

.aditional button:hover{
    background:#ffe484;
    color: #495057;
}

@media only screen and (max-width:991px){
    .header{
        text-align: center;
    }
    .aditional button{
        display: none;
        
    }
}

@media only screen and (max-width:768px){
    .header{
        flex-direction: column;
        height: 140px;
        justify-content: center;
        padding-top: 10px;
        position: relative;
    }
    .aditional{
        margin-top: 0;
        padding-top: 0;
        padding-bottom: 20px;
    }
}
@media only screen and (max-width:768px){
    .menu{
        padding-top: 0px;
        padding-bottom: 10px;
        text-align: center;
    }
}

.menu{
    flex-grow: 1;
    margin-top: 5px;
}

ul{
    list-style-type: none;
    padding-left: 10px;
}

 li  .active{
    color: white;
    
    font-family: 'Noto Sans', sans-serif;
    font-weight: 590;
}

 li  .active:hover{
    
    color: white;
    font-family: 'Noto Sans', sans-serif;
    font-weight: 590;
}

li {
    display: inline-block;
}


li a{
    color: #c4b4db;
    font-size: 15px;
    font-family: 'Noto Sans', sans-serif;
    text-decoration: none;
    padding: 5.5px;

}

li a:hover{
    color: #e9ecef;
}

.main{
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding-top: 70px;
}

.pmain{
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.pmain .p1main{
   margin-left: 8vw;
}

.pmain .p1main h1{
    color: #7952b3;
    font-size: 65px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
}

.pmain .p1main p, .pmain .p1main span{
    color: gray;
    font-size: 20px;
    padding-bottom: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 300;

}

.pmain .p1main span{
    font-size: 16px;
    font-weight: 400;
}

.p1main .button button {
    padding: 15px;
    padding-left:4vw;
    padding-right: 4vw;
    background: white;
    border-radius: 5px;
    font-size: 20px;
    display: inline;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 500;
    cursor: pointer;
    -webkit-transition: 0.2s;
    transition: 0.2s;
     margin-bottom: 20px;
}

.p1main .button .started{
    border:1.2px solid #7952b3;
    color: #7952b3;
    
}
.p1main .button .started:hover{
    background: #7952b3;
    color: white; 
}


.p1main .button .download{
  
    border: 1.2px solid #6c757d;
    color: #6c757d;
    margin-left: 10px;

}
.p1main .button .download:hover{
    background: #6c757d;
    color: white;

}

.main .pmain img{
    width:520px;
    height:430px;
    margin-right:10vh;
}

.main .smain{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-top: 20vh;
}
@media only screen and (max-width:1200px){
    .main .pmain img{
        width: 460px;
        height: 380px;
        margin-right: 6vh;
    }
    .pmain .p1main h1{
        font-size: 60px;
    }
}

@media only screen and (max-width:1050px){
    .pmain .p1main{
        margin-left: 5vw;
     }
     .pmain .p1main h1{
        font-size: 50px;
    }
    
}

@media only screen and (max-width:991px){
    .main .pmain img{
        width: 330px;
        height: 250px;
    }
    .pmain .p1main h1{
        font-size: 50px;
    }
    .pmain .p1main{
        margin-left: 12vw;
        margin-right: 8vw;
     }
     .p1main .button .download{
        margin-left: 0px;
    }
    .p1main .button{
        text-align: center;
        display: flex;
        flex-direction: column;
        
    }
    
}

@media only screen and (max-width:768px){
    .pmain{
        display: flex;
        flex-direction: column-reverse;
        text-align: center;
        margin-left: 6vw;
        margin-right: 6vw;
    }
    .pmain .p1main{
        margin-left: 0px;
        margin-right: 0px;
     }
     .main .pmain img{
        width: 230px;
        height: 200px;
        margin-left: 20px;
    }
    
   
}
@media only screen and (max-width:845px){
    .main .smain{
        display: flex;
        flex-direction: column;
    }
}
